.box-container {
    position: fixed;
    top: 0;
    width: calc(100% - 300px);
    padding: 4px 0px;
    z-index: 9999999999999;
    background-color: #fff;
    height: auto;
}

.container {
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    height: 50px;
}

.container .hotline-btn {
    display: inline-block;
}

.container .hotline-btn a {
    padding: 5px 30px 5px 15px;
    color: #fff;
    font-size: 1.2em;
    background-color: #e72e21;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 4px;
    transition: 0.15s ease-in-out;
}

.container .hotline-btn a span {
    margin-right: 15px;
}

.container .hotline-btn a span i {
    font-size: 1.2em;
}

.container .hotline-btn a:hover {
    background-color: #ee4538;
}

.container .message {
    color: #e72e21;
    font-size: 1.5em;
}

/* ----------------------------Menu--------------------------------- */

.box-nav {
    position: fixed;
    right: 0;
    top: 0;
    width: 300px;
    height: 100vh;
    box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.8);
    z-index: 9999999999999;
    background-color: #fff;
}

.box-nav .nav-bar-container {
    width: 100%;
    height: 100%;
    text-align: center;
}

.box-nav .nav-bar-container nav {
    margin-top: 100px;
}
.box-nav .nav-bar-container nav a {
    display: inline-block;
}

.box-nav .nav-bar-container nav a img {
    max-width: 60%;
}

.box-nav .nav-bar-container nav ul {
    padding: 0;
}

.box-nav .nav-bar-container nav ul li {
    list-style: none;
    border-bottom: 3.5px dashed #ddd;
    margin-top: -3.5px;
    cursor: pointer;
    transition: 0.15s ease-in-out;
}

.box-nav .nav-bar-container nav ul li:hover {
    background-color: #ddd;
}


.box-nav .nav-bar-container nav ul li:nth-child(1) {
    margin-top: 50px;
    border-top: 3.5px dashed #ddd;
}

.box-nav .nav-bar-container nav ul li a {
    text-decoration: none;
    color: #000;
    padding: 16px;
    width: 100%;
    font-size: 1.2em;
    font-weight: 400;
    text-shadow: 0px -1px 1px rgba(0, 0, 0, 0.8);
}

